import React from 'react'
import { NavLink, withRouter } from 'react-router-dom'

class Sidebar extends React.Component {
  render(){
    let currentPageName = (this.props.history.location.pathname).replace('/', '')

    return (
      <div className={`sidebar-wrap ${currentPageName}`}>

      <div className='main-logo'></div>
  
      <nav className='main-nav'>
        <ul className='main-menu'>
            <li><NavLink activeClassName='active team' to='/team' >Команда</NavLink></li>
            <li><NavLink activeClassName='active projects' to='/projects' >Проекты</NavLink></li>
            <li><NavLink activeClassName='active contacts' to='contacts' >Связаться</NavLink></li>
        </ul> 
      </nav>
      </div>
    )
  }
}

export default withRouter(Sidebar)
